*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --animation: 1s cubic-bezier(.075, .82, .165, 1);
  --black: #10181d;
}

.container {
  min-width: 320px;
  max-width: 480px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

body {
  color: var(--main-color);
  font-family: Roboto, sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

:root {
  --main-color: #000;
  --accent-color: #ff6b08;
  --container-bg: #e5e5e5;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.card {
  color: var(--main-color);
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

@media screen and (min-width: 1200px) {
  .card {
    font-size: 20px;
    line-height: 24px;
  }
}

@media screen and (min-width: 768px) {
  .card__pic {
    width: 336px;
    height: 455px;
  }
}

@media screen and (min-width: 1200px) {
  .card__pic {
    width: 395px;
    height: 574px;
  }
}

.card__information {
  flex-direction: column;
  gap: 5px;
  margin-top: 10px;
  margin-bottom: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .card__information {
    margin-top: 8px;
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .card__information {
    margin-top: 12px;
  }
}

.card__film-name {
  text-transform: uppercase;
}

.card__additional-information {
  color: var(--accent-color);
  display: flex;
}

.card__vl {
  border-left: 1px solid var(--accent-color);
  width: 0;
  margin-left: 5px;
  margin-right: 5px;
}

.image {
  width: 100%;
  height: 753px;
}

.backdrop__press {
  width: 100%;
  height: 100%;
  z-index: 1000;
  transition: opacity var(--animation), visibility var(--animation);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop__press.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.third-press__header {
  width: 100%;
  height: 70px;
  color: #fff;
  background-color: #dfdede;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-bottom: 30px;
  display: flex;
  box-shadow: 0 5px 10px #fff;
}

.third-press__header--text {
  background-color: var(--accent-color);
  border-radius: 5px;
  padding: 15px;
  font-size: large;
}

.backdrop__press--list {
  flex-flow: wrap;
  justify-content: center;
  align-items: start;
  gap: 30px;
  display: flex;
}

.third-backdrop {
  background-color: var(--black);
}

.btn__press {
  color: #fff;
  cursor: pointer;
  transition: background-color var(--animation), box-shadow var(--animation), opacity var(--animation), visibility var(--animation);
  background-color: #0000;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 12px 24px;
  font-size: 16px;
}

.btn__press:hover {
  box-shadow: 0px 8px 43px var(--accent-color);
  background-color: var(--accent-color);
  border: none;
}

.first-btn__press, .second-btn__press {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.press-overlay {
  width: 200px;
  height: 101%;
  color: var(--light-txt-clr);
  transition: transform var(--animation);
  background-color: #ff6b08e6;
  justify-content: center;
  align-items: center;
  padding: 0 24px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.press__item {
  width: 200px;
  transition: box-shadow var(--animation);
  background-color: #dfdede;
  border-radius: 5px;
}

.press__item:hover {
  box-shadow: 7px -7px 21px var(--accent-color);
}

.press__item:hover .press-overlay, .press__item:focus .press-overlay {
  transform: translateY(0);
}

.press-anim {
  position: relative;
  overflow: hidden;
}

.third-btn__press {
  border: 1px solid var(--accent-color);
  color: var(--accent-color);
}

.third-btn__press:hover {
  color: #dfdede;
}

